export default {
	data() {
		return {
			tel: '18553139389',
			code: '',
			pass: '',
			repeatPass: '',
			invite: '',
			datas: '获取验证码',
			btnState: false
		}
	},
	onLoad() {

	},
	onReady() {

	},
	methods: {
		getValidate() {
			if(!this.btnState) {
				if (this.tel == '') {
					uni.showToast({
						title: '手机号不能为空',
						icon: 'none',
						duration: 2000
					});
					return
				} else if (!(/^(13[0-9]|14[579]|15[0-3,5-9]|16[6]|17[0135678]|18[0-9]|19[89])\d{8}$/.test(this.tel))) {
					uni.showToast({
						title: '手机号格式不正确',
						icon: 'none',
						duration: 2000
					});
					return
				}
				
				let self = this
				this.datas = 60
				let datas = this.datas
				this.btnState = true
				let interval = setInterval(() => {
					if (--self.datas <= 0) {
						self.datas = '获取验证码'
						clearInterval(interval)
						self.btnState = false
					}
				}, 1000)
			}
		},
	}
}
